import skyconsFunc from 'skycons/skycons';
// const Skycons = require('skycons/skycons')(window);
const Skycons= skyconsFunc(window);
const skycons = new Skycons({"color": "#778287"});


const googleGeocodingKey = 'AIzaSyDThdmlF6wBbpCKnwlTjE4PfwGH1PNOEjw';
const forecastKey = 'fd04ff7341fc4b8ab8f77437a7c22c09'; 
let city;
const urls = [];
let data = [];
let darkSkyURL;
let googleGeocodingURL;
navigator.geolocation.getCurrentPosition((position) => {
    let {latitude, longitude} = position.coords;
    darkSkyURL = `https://crossorigin.me/https://api.darksky.net/forecast/${forecastKey}/${latitude},${longitude}?units=ca`;
    googleGeocodingURL = `https://maps.googleapis.com/maps/api/geocode/json?latlng=${latitude},${longitude}&key=${googleGeocodingKey}`;
    function getWeather(url) {
        return new Promise((resolve, reject) => {
            let xhr = new XMLHttpRequest();
            xhr.open('GET', url, true);
            xhr.send();

            xhr.onreadystatechange = function() {
                if (this.readyState === 4) {
                    resolve(JSON.parse(xhr.responseText));
                }
            };
        });
    }
    Promise.all([getWeather(darkSkyURL), getWeather(googleGeocodingURL)]).then((result) => {
        console.log(result);
        var temperature = document.querySelector('.temperature');
        var location = document.querySelector('.location');
        var dateContainer = document.querySelector('.date-container');
        var date = new Date();
        var condition = document.querySelector('.weather-conditions');
        var windSpeed = document.querySelector('.wind-speed span');
        var humidity = document.querySelector('.humidity span');
        var cloudCover = document.querySelector('.cloud-cover span');
        condition.innerHTML = result[0].currently.summary;
        windSpeed.innerHTML = Math.round(result[0].currently.windSpeed) + ' KPH';
        humidity.innerHTML = result[0].currently.humidity * 100 + ' %';
        cloudCover.innerHTML = result[0].currently.cloudCover * 100 + ' %';
        dateContainer.innerHTML = date.toString().slice(4, 7).toUpperCase() + '<br>' + date.toString().slice(8, 10);
        temperature.innerHTML = Math.round(result[0].currently.temperature) + "℃";
        location.innerHTML = result[1].results[1].address_components[0].long_name + ', ' + 
                             result[1].results[1].address_components[3].long_name;
        skycons.add("icon1", result[0].currently.icon);
        skycons.play();


    });
});


var toggler = document.querySelector('.toggler');
toggler.onclick = function(e) {
    console.log('clicked');
    // this.style.left =  this.parentNode.offsetWidth - this.offsetWidth + 'px'; 
    this.classList.toggle('f');
    console.log(this.parentNode.previousElementSibling);
    let prevEl = this.parentNode.previousElementSibling;
    let nextEl = this.parentNode.nextElementSibling;
    let temperature = document.querySelector('.temperature');
    if(prevEl.className === 'active') {
        prevEl.classList.remove('active');
        nextEl.classList.add('active');
        temperature.innerHTML = Math.round(temperature.innerHTML.replace(/(℃|℉)$/g, '') * 1.8 + 32) + '℉';
    } else {
        nextEl.classList.remove('active');
        prevEl.classList.add('active');
        temperature.innerHTML = Math.round((temperature.innerHTML.replace(/(℃|℉)$/g, '') - 32) / 1.8) + '℃';
    }
};




